.projects > * {
  margin: 0;
  padding: 0;
}

.projects {
  margin-top: 100px;
  width: 100%;
  text-align: center;
}

.container {
  margin: 10px auto;
}

.project-title {
  text-decoration: none;
  font-size: 20px;
  font-family: var(--nav-font);
  color: var(--green);
  border-bottom: 2px solid var(--green);
  transition: all 300ms ease;
}

.project-title:active {
  color: var(--purple);
}

.nextBtn {
  top: 0;
  right: 0;
}

.prevBtn {
  top: 0;
  left: 0;
}

.prevBtn,
.nextBtn {
  margin: 0 10px;
  min-width: 40px;
  border: none;
  background-color: transparent;
  color: var(--text-main-col);
  position: absolute;
  font-size: 30px;
  transition: all 200ms ease-in-out;
}

.nextBtn:active,
.prevBtn:active {
  transform: scale(0.5);
}

.snippetSizeTip {
  margin: 10px 0;
  text-align: center;
  font-size: 10px;
  color: var(--green);
  font-family: monospace;
  opacity: 0;
}

.snippetSizeTip.active {
  opacity: 1;
}

.codepenSnippet {
  margin: 30px auto;
  height: 80vh;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.codepenSnippet > iframe {
  width: 100%;
  height: 100%;
  border: none;
  color: var(--projects-col);
}

@media screen and (min-width: 768px) {
  .project-title {
    width: 25%;
    font-size: 30px;
  }

  .project-title:hover {
    border-color: var(--purple);
  }

  .codepenSnippet {
    width: 70%;
  }

  .prevBtn,
  .nextBtn {
    font-size: 50px;
  }

  .prevBtn:hover,
  .nextBtn:hover {
    cursor: pointer;
    color: var(--purple);
  }

  .prevBtn {
    left: 5%;
    top: 40%;
  }

  .nextBtn {
    right: 5%;
    top: 40%;
  }
}
